import { LoadingScreen } from '@riptech/minimal-ui/src/components/loading-screen';
import { useAppContext } from '@riptech/shared-ui/src/hooks/useAppContext';
import Head from 'next/head';
import { useRouter } from 'next/router';
import { useSession } from 'next-auth/react';
import type { ReactElement } from 'react';
import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import type { IntegrationLocalStorageType } from '../components/integrations/IntegrationCard';
import type { ComponentWithLayout } from '../hooks/useLayout';
import UserSelectors from '../redux/selectors/userSelectors';
import { INTEGRATION_LOCAL_STORAGE_REDIRECT_KEY, IS_CHROME_EXTENSION_UI_OR_DEV } from '../utils/constants';
import Routes from '../utils/routes';
const Index: ComponentWithLayout = () => {
  const {
    appName
  } = useAppContext();
  const router = useRouter();
  const userId = useSelector(UserSelectors.userIdSelector);
  const session = useSession();
  useEffect(() => {
    if (userId && session.status === 'authenticated') {
      const {
        source,
        redirect
      }: IntegrationLocalStorageType = JSON.parse(localStorage.getItem(INTEGRATION_LOCAL_STORAGE_REDIRECT_KEY) ?? '{}');
      if (IS_CHROME_EXTENSION_UI_OR_DEV && redirect) {
        localStorage.setItem(INTEGRATION_LOCAL_STORAGE_REDIRECT_KEY, JSON.stringify({
          redirect: false,
          source
        } satisfies IntegrationLocalStorageType));
        router.replace(Routes.INTEGRATIONS);
        return;
      }
      router.replace(IS_CHROME_EXTENSION_UI_OR_DEV ? Routes.REPORT_FETCHER : Routes.INSTALL);
    }
  }, [router, session.status, userId]);
  return <>
      <Head data-sentry-element="Head" data-sentry-source-file="index.tsx">
        <title>{appName}</title>
      </Head>
      <LoadingScreen data-sentry-element="LoadingScreen" data-sentry-source-file="index.tsx" />
    </>;
};
Index.getLayout = (page: ReactElement) => page;
export default Index;